import React from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core';
import {
    CssBaseline, Drawer, List, Icon,
} from '@material-ui/core';
import { NavigationBarItem, NavigationBarItems } from './SideNavbarItem';
import ListItemLink from '../shared/ListItemLink';

const drawerWidth = 70;

const useStyles = makeStyles((theme:Theme) => createStyles({
    root: {
        display: 'flex',
    },
    drawer: {
        width: drawerWidth,
        flexShrink: 0
    },
    drawerPaper: {
        width: drawerWidth,
        backgroundColor: theme.palette.primary.dark
    },
    navbarItemRoot: {
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        width: "inherit"
    },
    icon: {
        color: theme.palette.common.white
    }
}));

const SideNavigationBar : React.FC<NavigationBarItems> = (props:NavigationBarItems) => {
    const { navigationBarItems } = props;
    const classes = useStyles();

    return (
        <div>
            <CssBaseline />
            <Drawer
                anchor="left"
                className={classes.drawer}
                classes={{
                    paper: classes.drawerPaper
                }}
                variant="permanent"
            >
                <List>
                    {
                        navigationBarItems &&
                        navigationBarItems.map((value:NavigationBarItem) => (
                            <ListItemLink
                                href={value.link}
                                key={value.id}
                                title={value.tooltip}
                                data-testid={`item-${value.id}`}
                            >
                                {
                                    (value.icon || value.fontAwesomeClassName) &&
                                    <div className={classes.navbarItemRoot}>
                                        {
                                            value.fontAwesomeClassName
                                            ? <Icon 
                                                data-testid={`item-fontawesome-icon-${value.id}`}
                                                className={`${value.fontAwesomeClassName} ${classes.icon}`} />
                                            : <Icon 
                                                data-testid={`item-icon-${value.id}`}>
                                                    {value.icon}
                                                </Icon>
                                        }
                                    </div>
                                }

                                {
                                    value.label && 
                                    <span 
                                        data-testid={`item-label-${value.id}`}>
                                            {value.label}
                                    </span>
                                }
                            </ListItemLink>
                        ))
                    }
                </List>
            </Drawer>
        </div>
    );
}

export default SideNavigationBar;